import React, {useState} from 'react';
import './../Auth.css'
const axios=require('axios');

const Register = () => {

	const [name,setName] = useState('');
	const [password,setPassword] = useState('');
	const [email,setEmail] = useState('');

	const handleSubmit = (event) =>{
		event.preventDefault();
		console.log(name,password,email);
		axios.post('http://127.0.0.1:8000/register',
		 {
		  username:name,
		  email:email,
		  password:password
		 })
		.then(function (response) {
		  console.log(response);
		})
		}

	return(
	  <center>
	  <form onSubmit={handleSubmit} class="center">
		<input type='text' value={name} onChange={e => setName(e.target.value)}  name='username'></input><br/>
		<input type='password' value={password} onChange={e => setPassword(e.target.value)}  name='password'></input><br/>
		<input type='email' value={email} onChange={e => setEmail(e.target.value)}  name='email'></input><br/>
		<input type='submit' value='submit' class='button'/>
	  </form>
	  </center>
	);
  }
  
  export default Register